                <span class="helpBackground" style="float:left; margin-left: 1.5rem; margin-top: 0.5rem" v-tooltip.right ="{content:'所有交易直接在以太坊公链进行 每笔交易约需要20秒时间等待\n动态收益即股息 来自其它用户的交易金额 X 交易返利 / 已有货币总量 X 你当前的货币数量 交易返利比例参考币种介绍\n\n状态PENDING=正在处理\n状态CONFIRM=处理完毕\n状态REJECT=取消\n状态 + 号表示收益增加\n状态 - 号表示收益减少'}"/>
                <div>
