                  <a :href=this.$store.state.tokens.tokenProject.website target="_blank">
                  <span style="width: 25rem; display: inline-block;">官网：{{this.$store.state.tokens.tokenProject.website}}</span>
                  </a>
                  <!--文本超出显示...overflow: hidden; text-overflow: ellipsis -->
                  <a :href=this.$store.state.tokens.tokenProject.whiteBook target="_blank">
                  <span style="width: 37rem; display:inline-block; white-space: nowrap; overflow: hidden; text-overflow: ellipsis">白皮书：{{this.$store.state.tokens.tokenProject.whiteBook}}</span>
