<script>
import myTitle from '../components/TitleEx'
import myFoot from '../components/FootEx'
import tokenHeadVM from './tokenHead'
import tokenHeadjs from '../js/tokenHead'
import tokenBasejs from '../js/tokenBase'
import router from '@/router/index.js'
export default {
  name: 'tokenBody',
  components: {myTitle, myFoot, tokenHeadVM},
  data: function () {
    return {
      buyInput: '',
      sellInput: '',
      dividendInput: '',
      dividendPerShare: 0,
      tokens: ['SS', 'WPR', 'EVX', 'COVA', 'LAMB', 'tt'], // 全部token列表, 新token需要在此添加symbol 用于边栏显示token价格动态
      sellPriceIndexShow: ['sell9', 'sell8', 'sell7', 'sell6', 'sell5', 'sell4', 'sell3', 'sell2', 'sell1'], // 卖价序号显示字符
      buyPriceIndexShow: ['buy5', 'buy4', 'buy3', 'buy2', 'buy1'], // 买价序号显示字符
      pageIndex: 1,
      pageSize: 6,
      myTradingRecord: {
        tableData: [],
        tableColumns: [
          {field: 'time', title: '时间', titleAlign: 'center', columnAlign: 'center', width: 120, isResize: true},
          {field: 'token', title: '币种', titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'action', title: '操作', titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'price', title: '价格(ETH)', titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'amount',
            title: '交易金额(ETH)',
            titleAlign: 'center',
            columnAlign: 'center',
            width: 60,
            isResize: true,
            formatter:
              function (rowData, rowIndex, pagingIndex, field) {
                return rowData.amount > 0 ? Math.round(rowData.amount * 10000) / 10000 : ''
              }
          },
          {field: 'qty', title: '交易数量', formatter: function (rowData, rowIndex, pagingIndex, field) { return rowData.qty > 0 ? Math.round(rowData.qty * 10000) / 10000 : '' }, titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'myTokens', title: '我的数量', formatter: function (rowData, rowIndex, pagingIndex, field) { return rowData.myTokens > 0 ? Math.round(rowData.myTokens * 10000) / 10000 : '' }, titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'myDividends', title: '我的收益(ETH)', formatter: function (rowData, rowIndex, pagingIndex, field) { return rowData.myDividends > 0 ? Math.round(rowData.myDividends * 10000) / 10000 : '' }, titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'status', title: '状态', titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true}
        ]
      }
    }
  },
  computed: {
    contractStatusShow: function () {
      if (this.$store.getters.getOnlyAmbassadors) {
        return '天使阶段'
      } else {
        return '正常交易'
      }
    },
    purchaseLimitShow: function () {
      return Math.round(this.$store.state.tokens.tokenMetadata.purchaseLimit / 10000)
    },
    dividendFeeShow: function () {
      return Math.round((100 / this.$store.state.tokens.tokenMetadata.dividendFee) * 100) / 100
    },
    referrerFeeShow: function () {
      // ((100*dividenFee)/referrerFeeBydividendFee)/100
      let dividenFee = Math.round((100 / this.$store.state.tokens.tokenMetadata.dividendFee) * 100) / 100
      let referrerFeeBydividendFee = Math.round((this.$store.state.tokens.tokenMetadata.referrerFee) * 100) / 100
      let rest = Math.round((((100 * dividenFee) / referrerFeeBydividendFee) / 100) * 100) / 100

      // return Math.round((100 / this.$store.state.tokens.tokenMetadata.referrerFee) * 100) / 100
      return rest
    }
  },
  filters: {
    // 保留4位小数 用于文本插值
    numFilterFixed4 (value) {
      let rest = Math.round(value * 10000) / 10000
      return rest
    }
  },
  watch: {
    // 同一页面不同参数时刷新页面 用于左侧token列表
    // '$route' () {
    //   this.$router.go(0)
    // },
    buyInput: function () {
      // 正4位小数的
      this.buyInput = (this.buyInput.match(/^\d*(\.?\d{0,4})/g)[0]) || null
    },
    sellInput: function () {
      if (this.sellInput.substring(0, 1) !== '0') { // 不能以0开关
        this.sellInput = this.sellInput.replace(/[^\d]/g, '') // 只接整形数字
      } else {
        this.sellInput = ''
      }
    },
    dividendInput: function () {
      this.dividendInput = (this.dividendInput.match(/^\d*(\.?\d{0,4})/g)[0]) || null
      if (this.$store.getters.getTotalToken > 0 && this.dividendInput > 0) {
        this.dividendPerShare = this.dividendInput / this.$store.getters.getTotalToken
      }
    }
  },
  beforeCreate () {
    document.querySelector('body').setAttribute('style', 'margin: 0; padding:0;')
  },
  // 钩子函数 组件加载时执行
  created () {
  },
  // 当前路由参数改变时重新加载数据
  beforeRouteUpdate (to, from, next) {
    if (to.name === from.name && to.query !== from.query) {
      let token_, by_
      token_ = to.query.token
      this.initTokenData(token_, by_)
      next()
    } else {
      next()
    }
  },
  mounted () {
    // http://localhost:8080/tokenBody?token=SS2&ref=0x342d343eF....
    let token_, by_
    // 加载当前页的token
    if (typeof this.$route.query.token !== 'undefined') {
      token_ = this.$route.query.token
    } else {
      // 如果没有token参数就用系统默认token
      token_ = this.$store.getters.getSysTokenSymbol
    }
    // 加载推荐者
    if (typeof this.$route.query.by !== 'undefined' && window.web3.isAddress(this.$route.query.by)) {
      by_ = this.$route.query.by
    }

    tokenHeadjs.initContract(this.tokens) // 获取tokens例表信息

    this.initTokenData(token_, by_)
  },
  // 方法集合
  methods: {
    initTokenData (token, refBy) {
      // 清理
      tokenBasejs.accountClear()

      this.$store.commit('setTokenSymbol', token)
      this.$store.commit('setReferredBy', refBy)
      console.log(this.$options.name, 'log in token : ', token, 'referredBy', refBy)

      tokenBasejs.initContract(this.$store.getters.getTokenSymbol) // 获取当前页主体token信息

      // 加载token项目信息
      this.getTokenProject()

      // 加载公告
      this.getNotice()
    },
    tokenHeadListonClick (token) {
      this.$router.push({path: '/token', query: {token: token}})
      // window.location.href = '/token?token=' + token
    },
    getTradingTableData () {
      // // 从本地仓库中加载用户交易记录, 用于表格
      // let localTradingList = []
      // for (var i = 0; i < localStorage.length; i++) {
      //   var key = localStorage.key(i)
      //   // 在localStorage中以TradingRecord开头的key是交易记录
      //   if (key.indexOf('TradingRecord') > -1) {
      //     let keyValue = JSON.parse(localStorage.getItem(key))
      //     localTradingList.unshift({'time': keyValue.time, 'address': keyValue.address, 'type': keyValue.type, 'action': keyValue.action, 'amount': keyValue.amount, 'qty': keyValue.qty, 'state': keyValue.state}) // 前排插入
      //   }
