      //     this.pageIndex * this.pageSize
      //   )
      //   this.total = localTradingList.length
      // }

      // console.log('network', this.$store.getters.getNetwork, this.$store.getters.getCustomerAddress)
      // if (this.$store.getters.getNetwork !== '0') {
      //   // 从数据库加载交易记录至交易列表
      //   this.axios.get('/api/EXTrade?account=' + this.$store.getters.getCustomerAddress, { emulateJSON: true })
      //     .then((response) => {
      //       if (response.data.length > 0 && response.data.length < 100) {
      //         // 清空表格数据 重新加载
      //         this.$store.state.tokens.myTradingForTable.splice(0, this.$store.state.tokens.myTradingForTable.length)
      //         console.log('getTradingTableData1', response.data.length)
      //         for (let i = 0; i < response.data.length; i++) {
      //           let row = response.data[i]
      //           let record = {'sysID': row.sysID, 'token': row.token, 'time': row.time, 'account': row.account, 'type': 'load', 'action': row.action, 'price': row.price, 'amount': row.amount, 'qty': row.qty, 'status': row.status, 'hash': row.hash}
      //           this.$store.commit('setMyTradingForTable', record)
      //         }
      //       }
