  .background {
    text-align: center;
    margin: 0;
    padding: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background-color: #E5EBF5;
  }

  .liByNotice {
    text-align: left;
    list-style-type: none;
    width: 100%;
    border-radius: 0.2rem;
    background-color: white;
  }

  .liByTrading {
    list-style-type: none;
    text-align: left;
    margin-top: 0.5rem;
