    width: 3.3rem;
    text-align: left;
    margin-left: 1rem
  }
  .liByPrice {
    list-style-type: none;
    margin-top: 0.2rem;
    margin-left: 5rem;
    font-size: medium;
    height: 2rem;
    width: 13rem;
  }
  .liIndex {
    display: inline-block;
    width: 4rem;
    float: left;
    text-align: left;
  }
  .liItem {
    display: inline-block;
    width: 8rem;
    float: left;
    text-align: left;
    margin-left: 0.1rem;
  }

  .block_button {
    width: 15rem;
    height: 3rem;
    font-size: large;
    background: linear-gradient(to right, #8FBDFF, #B292FF);
    color: rgba(250, 250, 250, 1);
    border-radius: 2rem;
    margin-top: 1rem;
    cursor: pointer;
  }

  .input-text {
    margin: 0;
    padding: 0;
    height: 3rem;
    width: 13rem;
    font-size: 1rem;
    border: 1px solid #D8D8D8;
    border-image: none;
    text-indent: 0.2rem;
  }

  .input-submit {
    margin: 0;
    padding: 0;
