    background-color: #FF6B4A;
    border: 1px solid #FF6B4A;
    cursor: pointer;
    height: 3rem;
    width: 15rem;
    color: white;
    -moz-appearance: none;
    text-align: left;
    text-indent: 0.2rem;
    float: left;
  }
  .excessive {
    text-align: center;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #9980D6, #4BBF71);
  }
  .helpBackground{
    background-image:url('/static/pic/help.svg');
    background-size: cover;
    border: none;
    display: inline-block;
    width: 1.2rem;
    height: 1.2rem;
    cursor: help;
  }

</style>



