<template>
  <div id="show" class="title">
    <div style="float:left;margin-left:3rem;margin-top: 1.8rem">
      <span @click="goIndex" class="fontLinght_long" style="cursor: pointer">FREEBLOCK.LUXE</span>
    </div>
    <div style="float:right;margin-right:3rem;margin-top: 1.8rem">
      <span @click="NotLogin" style="cursor:default;">{{this.$store.getters.getNetworkShort}}</span>
      <span @click="goSeashells" style="margin-left: 1.2rem; cursor:pointer">seashells</span>
      <span @click="goWriteBook" style="margin-left: 1.2rem; cursor:pointer">白皮书</span>
      <span @click="goApply" style="margin-left: 1.2rem;cursor: pointer">申请上币</span>
    </div>
  </div>
</template>
<script>
import router from '@/router/index.js'
export default {
  methods: {
    goIndex () {
      router.push({path: '/'})
    },
    NotLogin () {
      if (this.$store.getters.getNetworkShort.indexOf('Not') > -1) {
        alert('you have not login Metamask')
      } else {
        alert('May the force be with you')
      }
    },
    goApply () {
      router.push({name: 'apply'})
    },
    goSeashells () {
      router.push({name: 'seashells'})
    },
    goWriteBook () {
      // router.push({name: 'WriteBook'})
      window.open('https://github.com/freeblockLuxe/Freeblock.luxe/blob/master/seashellsWhitebook.pdf')
    }
  }
}
</script>
<style scoped>
  .title {
    font-family: h;
    color: white;
    width: 100%;
    height: 5rem;
    margin: 0;
    padding: 0;
    text-align: center;
  }

  .fontLinght_long {
    font-size: large;
    text-shadow:
    0 0 1.5rem white,
    0 0 2.5rem white;
  }
</style>



